.h-half {
    position: absolute;
    top: 50%;
    transform: translate(25%,-50%);
}

/*mobile h-half*/

@media only screen and (max-width: 640px) {
    .h-half {
        position: absolute;
        transform: translate(0%,-30%);
    }
}
.pic-size {
    width: 100%;
}

.height-80vh {
    height: 60vh;
}
.container-iframe-16-9 {
  position: relative;
  overflow: hidden;
  width: 60%;
  padding-top: 33.75%; /* 16:9 Aspect Ratio */
}

.container-iframe-4-3 {
  position: relative;
  overflow: hidden;
  width: 60%;
  padding-top: 45%; /* 4:3 Aspect Ratio */
}

.container-iframe-3-2 {
  position: relative;
  overflow: hidden;
  width: 60%;
  padding-top: 40%; /* 3:2 Aspect Ratio */
}

.container-iframe-audio {
  position: relative;
  overflow: hidden;
  width: 60%;
  padding-top: 10%; /* Audio bar Aspect Ratio */
}

.container-iframe-no {
  position: relative;
  overflow: hidden;
  width: 60%;
}
  
/* Then style the iframe to fit in the container div with full height and width */
.responsive-iframe {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
}
